<style lang="less" scoped>
  .container-set {
    padding: 50px;
    .select{
      display: block;
    }
  }
</style>

<template>
  <div class="container-set">
    <el-form class="container" ref="form" label-width="80px" >

      <el-form-item  label="新增仓库">
        <el-input clearable v-model="user" placeholder="仓库名称"></el-input>
      </el-form-item>

      <el-form-item label="fork" width="500px">
        <el-select v-model="forkUser" placeholder="fork" class="select">
          <el-option label="-" value="" key="1"></el-option>
          <el-option v-for="item in storeList" :label="item.user" :value="item.user" :key="item.user"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="proxyHost">
        <el-input clearable
                  placeholder="代理仓库名称"
                  v-model="proxy.host"
                 >
        </el-input>
      </el-form-item>
      <el-form-item label="运行端口">
        <el-input clearable
                  placeholder="默认80"
                  v-model="proxy.port"
                  >

        </el-input>
      </el-form-item>

      <el-form-item label="proxyCache">
        <el-switch
          v-model="proxy.cache"
          active-color="#13ce66"
         >
        </el-switch>
        <span> 代理请求成功后是否缓存数据</span>
      </el-form-item>
      <el-form-item label="运行">
        <el-switch
          v-model="proxy.start"
          active-color="#13ce66"
          >
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit">创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import mixin from './mixin'

  export default {
    data() {
      return {
        loading: false,
        user: '',
        forkUser: '',
        proxy: {
          host: 'default',
          port: 80,
          cache: false,
          start: false,
        }
      };
    },
    computed: {
      ...mapState(['storeList']),
      params() {
        return {
          user: this.user,
          forkUser: this.forkUser,
          proxy: this.proxy
        }
      }
    },
    created() {

    },
    mixins: [mixin],
    methods: {

      submit() {
        if (!this.test(this.params)) return;
        this.post('/setting/addStore', this.params).then(json => {
          this.dispatch('getStoreList')
        })
      },
    },
  }

</script>



